@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

@font-face {
  font-family: 'icons';
  src: url(/react-table-control/static/media/icons.423d006d.eot);
  src: url(/react-table-control/static/media/icons.423d006d.eot) format("embedded-opentype"), url(/react-table-control/static/media/icons.ab3d38bf.woff2) format("woff2"), url(/react-table-control/static/media/icons.c03dc181.woff) format("woff"), url(/react-table-control/static/media/icons.74411c9c.ttf) format("truetype"), url(/react-table-control/static/media/icons.b9d51453.svg) format("svg");
  font-weight: normal;
  font-style: normal; }

.tc-icon-group-arrow-collapsed:before {
  content: '\e800'; }

.tc-icon-close:before {
  content: '\e801'; }

.tc-icon-group-arrow-expanded:before {
  content: '\e802'; }

.tc-icon-sort-arrow-down:before {
  content: '\e803'; }

.tc-icon-sort-arrow-up:before {
  content: '\e804'; }

.tc {
  overflow: hidden;
  background-color: white; }

.tc-table {
  font-size: 14px;
  width: 100%;
  border-collapse: collapse;
  display: flex;
  flex-direction: column;
  height: 100%; }

.tc-thead {
  background-color: #F1F5F7; }

.tc-thead-cell {
  padding: 15px 20px;
  color: #747D86; }

.tc-thead-cell-content {
  display: flex;
  align-items: center; }

.tc-tbody {
  display: block;
  overflow-y: scroll;
  height: 100%; }

.tc-cell {
  padding: 15px 20px;
  color: #353C44; }

.tc-icon {
  font-weight: 100;
  font-size: 10px;
  font-family: 'icons'; }

.tc-empty-cell {
  width: 40px;
  min-width: 40px; }

.tc-tr {
  display: table;
  width: 100%;
  table-layout: fixed; }

.tc-row {
  box-sizing: border-box;
  border-bottom: 1px solid #F9FBFC;
  border-top: 1px solid #F9FBFC; }

.tc-row-selected {
  background-color: #F7FcFd; }

.tc-group-row {
  background-color: #F9FBFC;
  box-sizing: border-box;
  border-bottom: 1px solid white;
  border-top: 1px solid white; }

.tc-group-column {
  padding: 8px 10px; }

.tc-group-column-content {
  display: flex;
  align-items: center; }

.tc-icon-group-arrow {
  padding: 10px 5px 10px 10px;
  margin-right: 5px;
  cursor: pointer; }

.tc-icon-sort {
  margin-left: 3px; }

.tc-pointer {
  cursor: pointer; }

body {
  margin: 0;
  padding: 0; }

.demos {
  font-family: 'Roboto',  'Helvetica Neue Light', 'Open Sans', Helvetica;
  display: flex; }
  .demos nav {
    width: 350px;
    box-sizing: border-box;
    border-right: 1px solid #CBDCE4; }
    .demos nav .logo-container {
      padding: 20px 50px;
      border-bottom: 1px solid #CBDCE4;
      display: flex; }
      .demos nav .logo-container a {
        line-height: 27px; }
      .demos nav .logo-container b, .demos nav .logo-container a {
        margin-right: 10px; }
    .demos nav ul {
      list-style-type: none;
      padding: 0;
      box-sizing: border-box; }
      .demos nav ul a {
        display: block;
        padding: 10px 0 10px 40px;
        text-decoration: none; }
        .demos nav ul a .menu-button {
          display: flex; }
          .demos nav ul a .menu-button .menu-icon {
            width: 40px; }
          .demos nav ul a .menu-button .menu-button-inner {
            width: 185px; }
        .demos nav ul a.active {
          pointer-events: none;
          padding: 10px 0 10px 36px;
          border-left: 4px solid #353C44;
          color: #353C44; }
  .demos main {
    width: calc(100% - 350px);
    box-sizing: border-box; }
    .demos main .simulator-content {
      background: #ECF0F4;
      padding: 28px 80px 60px; }
      .demos main .simulator-content h1 {
        margin: 0 0 30px 20px;
        font-size: 22px; }
      .demos main .simulator-content .tc {
        border-radius: 12px;
        box-shadow: 5px 15px 30px rgba(0, 0, 0, 0.1); }
      .demos main .simulator-content .tc-tbody {
        max-height: 600px; }
    .demos main .code {
      padding: 60px 80px; }

